.header-top {
    .contact-box {
        margin-right: auto;
        margin-left: 30px;

        .mail+span {
            padding-left: 0;
            font-size: remMM(12);
        }

        a {
            font-weight: 400;
            font-size: remMM(12);
        }

        .mail {
            margin-top: 0;
        }

        @media (max-width: 767px) {
            display: none;
        }
    }
}

.cart-inner {
    width: 100%;
    max-width: 1035px;
    margin: 0 auto;
    padding: 121px 0 20px 0;
    position: relative;

    &::before {
        content: "";
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        height: 100%;
        width: 1644px;
        max-width: 100vw;
        display: block;
        position: absolute;
        z-index: -1;
        background-color: $color-font-second;
    }

    @media screen and (max-width: 1000px) {
        padding: 70px 0 20px 0;

    }
}

.cart-inner h4 {
    font-size: remMM(24);
    margin: 0 0 18px 0;
    padding-bottom: 45px;
    font-weight: 700;
    // border-bottom: 1px solid $color-font-100;
    // padding-bottom: 10px;
}

