.cart-header {
    display: flex;
    justify-content: space-between;
    max-width: 1248px;
    list-style: none;
    padding: 62px 0 55px 0;
    margin: 0 auto;
    text-align: center;
    position: relative;
    gap: 64px;
    // background-color: $color-font;

    &::before {
        content: "";
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        height: 100%;
        width: 1644px;
        max-width: 100vw;
        display: block;
        position: absolute;
        z-index: -1;
        background-color: $color-font;
    }

    @media (max-width:1000px) {
        gap: 15px;
        padding: 40px 0 40px 0;
    }

    @media (max-width: 767px) {
        gap: 5px;
        padding: 20px 0 20px 0;
    }

    strong {
        font-weight: 400;
        position: relative;
    }

    .step {
        // flex: 1 1 auto;
        // border: 3px solid #B2B2B2;
        // border-radius: 5px;
        // overflow: hidden;
        // background-color: $color-page-bg;

        a,
        strong {
            display: flex;
            align-items: center;
            padding: 0 0 0 80px;
            gap: 10px;
            position: relative;
            // border-radius: 5px;
            text-align: center;
            justify-content: center;
            font-weight: 700;
            color: $color-font-second;
            font-size: remMM(24);
            line-height: 1em;

            &::before {
                content: "";
                position: absolute;
                width: 62px;
                height: 63px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                flex-wrap: nowrap;
                line-height: 0;
                margin: 0;
                left: 0;
                top: 50%;
                font-size: remMM(18);
                line-height: 1em;
                color: $color-font;
                font-weight: 700;
                transform: translateY(-50%);
                background-position: center center;
                background-repeat: no-repeat;
                background-size: contain;
            }

            // &::after {
            //     content: "";
            //     position: absolute;
            //     width: 24px;
            //     height: 24px;
            //     display: block;
            //     line-height: 0;
            //     margin: 0;
            //     right: -44px;
            //     top: 50%;
            //     transform: translateY(-50%);
            //     background-image: url(../img/icon/arrowNext.svg);
            //     background-position: center center;
