                padding: 10px 5px 10px 70px;

                &::before {
                    margin: 0;
                }

                &::after {
                    display: none;
                }
            }

            @media (max-width: 767px) {
                padding: 20px 20px 20px 40px;

                &::before {
                    left: 10px;
                }
            }

            @media (max-width: 600px) {
                padding: 64px 10px 8px 10px;
                font-size: remMM(16);

                &::before {
                    left: 50%;
                    top: 0px;
                    transform: translateX(-50%);
                }
            }

            @media (max-width: 480px) {
                font-size: remMM(16);

                &::before {}
            }
        }

        a {
            text-decoration: none;

            &:hover {
