            }
        }

        &.active {
            // background-color: #FFFDDF;
            // border: 3px solid $black;

            strong {
                // font-weight: 700;
            }

            a,
            strong {
                // color: $color-font;
            }
        }
    }

    // .step-2 {
    // 	position: relative;
