    // 		position: absolute;
    // 		top: 50%;
    // 	}
    // 	&::before {
    // 		left: 0;
    // 		transform: translateX(-50%);
    // 	}
    // 	&::after {
    // 		right: 0;
    // 		transform: translateX(50%);
    // 	}
    // }
    .step-1 {

        a,
        strong {


            &::before {
                content: "01";
                // background-image: url(../img/icon/cart-inactive-2.svg);
            }
        }

        &.active {

            a,
            strong {
                &::before {
                    // background-image: url(../img/icon/cart-active.svg);
                }
            }
        }
    }

    .step-2 {

        a,
        strong {


            &::before {
