// CART LAYOUT
.cart-row {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 16px;
    justify-content: flex-start;
    flex-direction: column;


    .col-md-8 {
        flex: unset;
        width: 100%;
        max-width: 100%;

        &#checkoutContent {
            width: 100%;
            max-width: 612px;

            @media (max-width: 1000px) {
                max-width: 100%;
                flex: unset;
            }
        }
    }

    .col-md-4 {
        flex: unset;
        width: 100%;
        max-width: 100%;
    }

    @media (max-width: 767px) {
        gap: 20px;
    }

    @media (max-width: 1300px) {
        flex-wrap: wrap;
    }

    @media (max-width: 1000px) {
        .col-md-4 {
            // max-width: 600px;
            width: 100%;
        }
    }

    @media (max-width: 767px) {
        .col-md-4 {
            max-width: 100%;
            width: 100%;
        }
