.summary-wrapper {
    .free-gift {
        h4 {
            padding: 0;
        }

        .free-gifts-wrapper {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            flex-wrap: nowrap;
            gap: 24px;
            margin-bottom: 16px;

            .free-gifts-img {
                img {}
            }

            .free-gift-label {
                .free-gift-name {
                    text-decoration: none;
                    font-weight: 600;
                    margin-bottom: 7px;
                    display: block;
                    font-size: remMM(18);
                    text-decoration: none;
                }
            }
        }
    }

    .upper-summary {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
        flex-wrap: nowrap;
        margin-bottom: 56px;

        @media screen and (max-width: 767px) {
            flex-direction: column;
            gap: 20px;
            margin-bottom: 20px;
            align-items: center;
            justify-content: flex-start;
        }

        .cart-summary {
            margin-bottom: 0;

            h4 {
                margin: 0;
                font-size: remMM(16);
                line-height: 1.2em;
                font-weight: 400;
                cursor: pointer;
                position: relative;
                padding: 0 0 0 32px;

                &.active {
                    margin-bottom: 16px;

                    &::before {
                        background-color: $color-accent;
                    }

                    &::after {
                        // content: url(../img/icon/check.svg);
                        display: block;
                        line-height: 0;
                        width: 12px;
                        height: 13px;
                        left: 4px;
                        top: 1px;

                        position: absolute;
                    }
                }

                &::before {
                    content: "";
                    left: 0;
                    top: -4px;
                    width: 24px;
                    height: 24px;
                    position: absolute;
                    display: block;
                    border: 1px solid #9E9FA0;
                    border-radius: 2px;
                }
            }

            .discount-coupon {
                display: none;

                &.active {
                    display: inline-block;
                }
            }
        }

        .price-holder {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            flex-wrap: nowrap;
            gap: 10px;

            @media screen and (max-width: 767px) {
                width: 100%;
            }

            .save-holder {
                font-size: remMM(16);
                line-height: 1.2em;
                color: $color-font;
                font-weight: 600;
            }

            .price-wrapper {
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                align-items: flex-end;
                flex-wrap: nowrap;
                padding: 0;
            }
        }
    }
}

