// CART TABLE
// .cart-table,
// #checkoutContent .cart-content {
// 	background-color: $color-font-050;
// 	padding: 50px;
// }
table.cart-table {
    width: 100%;
    // border-bottom: 1px solid $border-primary;
    // margin-bottom: 20px;
    display: block;

    h2 {
        font-size: remMM(28);
        margin: 0 0 20px 0;
        padding-bottom: 20px;
    }

    tbody {
        width: 100%;
        display: block;

        // border-top: 3px solid $black;
        // padding-top: 15px;
        tr {
            td {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;

                &.p-quantity {
                    align-items: center;
                    justify-content: center;
                    flex-wrap: nowrap;
                    flex: unset;
                }

                &.p-availability {
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    flex-wrap: nowrap;

                    .availability-amount {
                        display: inline-block;
                    }
                }

                &.p-price {
                    flex-direction: row;
                    align-items: center;
                    justify-content: flex-end;
                    flex-wrap: nowrap;
                    // display: none;
                }

                &.p-total {
                    flex-direction: row;
                    justify-content: flex-end;
                    align-items: center;
                    gap: 20px;

                    @media (max-width: 1000px) {
                        gap: 10px;
                    }
                }

                &.p-save {
                    flex: 1 1 80px;
                    margin-left: 50px;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    justify-content: center;
                    flex-wrap: nowrap;
                    gap: 5px;
                    max-width: 80px !important;

                    span {


                        &:nth-of-type(1) {
                            font-size: remMM(14);
                            color: $black;
                            font-weight: 600;

                            display: inline-block;
                        }

                        &:nth-of-type(2) {
                            font-size: remMM(14);
                            color: $black;
                            font-weight: 600;

                            display: inline-block;
                        }
                    }

                    @media screen and (max-width: 1000px) {
                        margin-left: 20px;
                    }
                }

                // &.{}
            }

            &:first-of-type {
                // background-color: rebeccapurple;

                .p-total,
                .p-price,
                .p-quantity,
                .p-save {
                    position: relative;

                    .p-label {
                        position: absolute;
                        display: inline-block;
                        bottom: calc(100% + 28px);
                        right: 39px;
                        font-size: remMM(14);
                        color: $black;
                        line-height: 1.2em;
                        font-weight: 400;
                        white-space: nowrap;

                        @media screen and (max-width: 1000px) {
                            right: 5px;
                        }
                    }

                    @media screen and (max-width: 767px) {
                        position: unset;

                        .p-label {
                            display: none;
                        }
                    }
                }

                .p-total {
                    .p-label {}
                }

