// CART FOOTER
.ordering-process-footer {
    padding: 40px 0;
    border-top: 1px solid $color-font-100;
    // background: linear-gradient(180deg, #f5f4f4 0%, rgba(255, 255, 255, 0) 100%);

    .container {
        max-width: calc(1280px + 4rem);
        display: flex;
        align-items: center;
        gap: 20px;
        justify-content: space-between;
    }

    .contact-box {
        margin: 0;
        display: flex;
        flex-direction: column;
        position: relative;

        &::before {
            content: "";
            left: -20px;
            height: 50px;
            width: 1px;
            top: 50%;
            transform: translateY(-50%);
            background-color: $color-font-200;
            display: block;
            position: absolute;
        }

        .mail {
            margin: 0;

            &+span {
                font-size: remMM(12);
            }
        }

        a {
            font-size: remMM(16);
            font-weight: 700;
        }

        .tel {
            flex-wrap: wrap;

            span {
                flex-basis: 100%;
                padding-left: 34px;
                font-size: remMM(12);
            }
        }
    }

    .secure-payment {
        span {
            display: block;
            margin-bottom: 10px;
            line-height: emMM(24, 16);
        }

        img {
            max-width: 192px;
        }
    }
}

@media (max-width: 900px) {
    .ordering-process-footer {
        .container {
            flex-direction: column;
            align-items: flex-start;
        }
    }
}

@media (max-width: 767px) {
    .ordering-process-footer {
        padding: 30px 0;

        .secure-payment {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
        }

        .contact-box {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            flex-wrap: nowrap;
        }
    }

    .cart-header-wrapper {
        padding: 0;
    }

    .cart-header {

        &::before,
        &::after {
            // display: none;
        }
    }

    table.cart-table {
        tr:not(.related) {
            display: grid;
            grid-template-columns: minmax(1px, 150px) auto;
            padding: 10px 0;
            gap: 0;
            position: relative;
        }

        .cart-p-image {
            grid-column: 1/2;
            grid-row: 1/3;
            margin-right: 20px;
            max-width: 60px;
            min-width: 60px;
        }

        .p-name {
            grid-column: 2/3;
            grid-row: 1/2;
            margin-bottom: 7px;
        }

        .p-availability {
            grid-column: 2/3;
            grid-row: 2/3;
            margin-bottom: 7px;
        }

        .p-quantity {
            grid-column: 2/3;
            grid-row: 3/5;
            justify-self: end;
            padding-right: 50px;

            .quantity {
                &::after {
                    display: none;
                }
            }
        }

        .p-price {
            grid-column: 1/2;
            grid-row: 3/4;
        }

        .p-total {
            grid-column: 1/2;
            grid-row: 4/5;
        }

        form.inline {
            position: absolute;
            bottom: 34px;
            right: 0;

            @media (max-width: 767px) {
                bottom: 21px;
                right: 15px;

            }
        }

        .cart-related-product {
            flex-direction: column;
            padding: 20px;
            gap: 10px;

            .cart-related-info {
                display: grid;
                grid-template-columns: minmax(60px, 65px) auto;
                width: 100%;
            }

            .cart-related-img {
                grid-column: 1/2;
                grid-row: 1/3;
                width: 60px;
                height: 60px;
            }

            .cart-related-name {
                grid-column: 2/3;
                grid-row: 1/2;
            }

            .cart-related-availability {
                grid-column: 2/3;
                grid-row: 2/3;
                margin-left: 0;
            }

            .cart-related-button {
                width: 100%;
                justify-content: space-between;
            }
        }

        tr.related {
            padding-left: 0;
        }
    }

    // .summary-wrapper,
    // #summary-box {
    // 	padding: 50px 20px;
    // 	margin-left: -20px;
    // 	margin-right: -20px;
    // }
    .back-shopping-link {
        margin-top: 50px;
    }

    .sidebar-in-cart .extra.delivery {
        order: -1;
        // margin-left: -20px;
        // margin-right: -20px;
        // margin-bottom: -21px;
    }

    table.cart-table .p-availability,
    table.cart-table .p-price,
    table.cart-table .p-total {
        text-align: left;
    }

    table.cart-table tr {
        align-items: start;
    }

    table.cart-table .p-total {
        justify-content: flex-start;
    }

    table.cart-table .p-availability {
        font-size: remMM(12);
    }

    .shipping-billing-table .radio-wrapper .payment-logo {
        display: none;

        img {
            // width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

// APPLE PAY
.radio-wrapper[data-submethod='applepay'] {
    display: none;
}

.id--17 {

    input[type='text'],
    input[type='email'],
    input[type='password'],
    input[type='tel'],
    input[type='search'],
    input[type='number'],
    textarea,
    select {
        font-size: remMM(16);

        &.js-error-field,
        &.error-field {
            border-color: #E82B37;
            //background-image: url(../img/icon/input-error.svg) !important;
            background-repeat: no-repeat;
            background-position: right 16px center;
            background-size: 16px;

        }

        &.validated {
            //background-image: url(../img/icon/input-right.svg) !important;
            background-repeat: no-repeat;
            background-position: right 16px center;
            background-size: 16px;
        }
    }

    .cart-inner {
        max-width: 100%;
        padding: 55px 0 20px 0;

        .cart-row {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            flex-wrap: wrap;
            align-items: flex-start;

            #checkoutContent {
                max-width: 450px;

                @media screen and (max-width: 980px) {
                    max-width: 100%;
                }

                .box {
                    margin-bottom: 0;
                }



                h4 {
                    padding: 0;
                    margin: 0 0 10px 0;
                }
            }

            #submit-order {
                margin-left: 0;
            }


        }
    }

    .order-button-suffix {
        display: none;
    }

    .order-button-text {
        text-transform: unset !important;
    }
}

.account-part {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(420px, 1fr));
    justify-items: center;
    align-items: center;
    grid-gap: 24px;
    margin-bottom: 55px;

    .holder {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        gap: 25px;
        padding: 40px 10px;
        width: 100%;
        border: 1px solid #E4E4E4;
        border-left: 4px solid $color-font;

        &.login {}

        &.register {
            h4 {
                max-width: 260px;
            }
        }

        h4 {
            padding: 0;
            margin: 0;
        }

        .btn {}
    }

    @media screen and (max-width: 767px) {
        grid-template-columns: 1fr;

        .holder {
            padding: 20px 10px;
            gap: 15px;
        }
    }
}

