// TOP KATEGORIE
.top-categories {
    display: grid;
    grid-template-columns: repeat(12, minmax(1px, 1fr));
    grid-template-rows: 350px 328px;
    gap: 30px;

    .category-item {
        grid-column-end: span 3;
        position: relative;
        border-radius: 5px;
        overflow: hidden;

        &.wide {
            grid-column-end: span 4;
        }

        .main-link {
            font-size: remMM(20);
            font-family: $heading-font-family;
            font-weight: 700;
            text-transform: uppercase;
            margin: 0;
            padding: emMM(10, 20);
        }

        ul {
            padding: 0;
            margin: 0;
            max-width: minmax(157px, 57%);
            list-style: none;

            li {
                padding-left: calc(4px + 0.4em);

                a {
                    position: relative;

                    &::before {
                        content: '';
                        display: block;
                        width: 4px;
                        height: 4px;
                        background-color: $color-font;
                        border-radius: 50%;
                        position: absolute;
                        left: 0;
                        top: calc(0.5em + 0.3125em + 2px);
                    }
                }
            }
        }

        a {
            text-decoration: none;
            padding: emMM(5) emMM(10);
            display: block;

            &.parent-link {
                color: $color-accent;
                font-weight: 700;
            }

            &:hover {
                text-decoration: underline;
            }
        }

        &__texts {
            position: absolute;
            top: 30px;
            left: 30px;
            bottom: 30px;
            right: 30px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top left;
        }
    }

    @media (max-width: 1240px) {
        grid-template-columns: repeat(7, minmax(1px, 1fr));
        grid-template-rows: auto;
        grid-auto-flow: dense;
    }

    @media (max-width: 840px) {
        grid-template-columns: repeat(2, minmax(1px, 1fr));
        grid-template-rows: 350px 350px 350px 350px;

        .category-item {
            grid-column-end: span 1;

            &.wide {
                grid-column-end: span 1;
            }
        }
    }

    @media (max-width: 767px) {
        gap: 1px;
        display: flex;
        flex-direction: column;

        .category-item {
            &__texts {
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;

                ul,
                .parent-link {
                    display: none;
                }

                .main-link {
                    font-size: remMM(18);
                    padding: 30px;
                    display: flex;
                    align-items: center;
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}

// COUNTDOWN BANNER
.countdown-banner {
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    color: $color-page-bg;
    height: 487px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    h2 {
        margin: 0 0 10px;
        text-transform: initial;

        em {
            display: block;
            font-size: emMM(16, $h2);
            line-height: emMM(20, 16);
            font-style: normal;
        }
    }

    &__overlay {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        max-width: 342px;
        width: 100%;
        background-color: rgba(#eb2121, 0.8);
        padding: 70px 36px;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        .countdown-banner__title {
            margin-bottom: auto;
            font-size: 102px;
            line-height: 119px;
            text-transform: uppercase;

            span {
                font-size: 49px;
                line-height: 57px;
                display: block;
            }
        }

        &::before,
        &::after {
            content: '';
            position: absolute;
            display: block;
            top: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(#eb2121, 0.25);
            z-index: 1;
        }

        &::before {
            left: -10px;
        }

        &::after {
            left: -20px;
        }

        &>div,
        &>h2,
        &>p {
            z-index: 2;
        }
    }

    &__text {
        font-weight: 700;
        text-transform: uppercase;
        margin: 0 0 32px;
    }

    &__timer {
        display: flex;
        gap: 10px;

        &-item {
            border-radius: 5px;
            overflow: hidden;
            background-color: #ffdada;
            padding: 10px 20px;
            color: $color-font;
            flex: 1;

            span {
                display: block;
            }
        }

        &-label {
            color: $color-font-700;
        }

        &-value {
            font-size: remMM(36);
            margin-bottom: 5px;
            font-weight: 700;
            font-family: $heading-font-family;
        }
    }

    &__texts {
        position: absolute;
        left: 50px;
        bottom: 50px;
        max-width: 500px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    picture {
        position: relative;
        display: block;
        height: 100%;
    }

    picture::before {
        content: '';
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        background: linear-gradient(61deg, rgba(0, 0, 0, 0.4) 22.11%, rgba(0, 0, 0, 0) 53.69%);
    }

    @media (max-width: 980px) {
        height: 370px;

        &__overlay {
            padding: 30px;

            .countdown-banner__title {
                font-size: 72px;
                line-height: 84px;

                span {
                    font-size: 36px;
                    line-height: 42px;
                }
            }
        }

        &__texts {
            left: 30px;
            bottom: 30px;
            max-width: calc(100% - 342px - 60px);
        }
    }

    @media (max-width: 767px) {
        height: auto;

        &__overlay {
            position: initial;
            background-color: #eb2121;
            max-width: unset;
            width: 100%;

            &::before,
            &::after {
                display: none;
            }

            .countdown-banner__title {
                font-size: remMM(30);
                line-height: emMM(36, 30);
                margin-bottom: 16px;

                span {
                    font-size: inherit;
                    display: inline;
                }
            }
        }

        &__text {
            margin-bottom: 16px;
        }

        &__texts {
            bottom: calc(261px + 20px);
            max-width: unset;
            right: 20px;
            left: 20px;
        }

        picture::before {
            background: linear-gradient(61deg, rgba(0, 0, 0, 0.4) 22.11%, rgba(0, 0, 0, 0) 53.69%),
                linear-gradient(61deg, rgba(0, 0, 0, 0.4) 22.11%, rgba(0, 0, 0, 0) 53.69%);
        }
    }
}

// VERNOSTNi KLUB
.loyalty-club {
    padding: 100px 0;
    position: relative;
    width: calc(100vw - var(--scrollbar-width));
    left: 50%;
    transform: translateX(-50%);
    //background: url(../img/vernostny-klub-bg.jpg) no-repeat center center;
    background-size: cover;

    .images {
        display: grid;
        gap: 30px;
        grid-template-columns: repeat(2, minmax(1px, 1fr));
        align-items: center;

        .image-wrapper {
            &.height-span {
                grid-row-end: span 2;
            }
        }

        img {
            display: block;
            border-radius: 5px;
        }
    }

    .columns {
        gap: 0;
    }

    .column:not(.images) {
        padding: 0 100px;

        ul {
            list-style: none;
            padding: 0;
            margin: 0;

            li {
                display: flex;
                gap: 0.4em;
                align-items: center;
                padding: 20px 0;
                border-bottom: 1px solid $color-font-300;

                &.highlight {
                    color: $color-accent;
                    font-weight: 700;
                    font-size: remMM(24);
                }
            }
        }
    }

    h2 {
        margin: 0 0 emMM(20, $h2);
    }

    p {
        color: $color-font-700;
    }

    @media (max-width: 1200px) {
        .columns {
            gap: 30px;
        }

        .column:not(.images) {
            padding: 0;
        }
    }

    @media (max-width: 767px) {
        padding: 50px 0;

        .columns {
            flex-direction: column-reverse;
            gap: 10px;

            &.col-2 .column {
                flex-basis: auto;
            }
        }

        .images {
            gap: 15px;
        }
    }
}

