// CATEGORY
.banner-category {
    grid-column-end: span 2;

    &.second {
        grid-column-end: span 1;

        .extended-banner-texts {
            padding: 20px 35px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;

            .extended-banner-title {
                font-size: remMM(24);
                color: $color-primary;
                margin-bottom: 15px;
                text-align: left;
            }

            .extended-banner-text {
                color: $color-font-second;
                margin-bottom: 30px;
                text-align: left;
                font-size: remMM(12);

                strong {}
            }

            .extended-banner-link {
                padding: emMM(7) emMM(15) emMM(11) emMM(15);

                &:hover,
                &:focus {}
            }
        }

        @media (max-width: 767px) {
            grid-column-end: span 2;
        }
    }

    &.first {}



    a {
        height: 100%;
        // border-radius: 10px;
        overflow: hidden;
    }

    a,
    img {
        height: 100%;
        position: relative;
    }

    .extended-banner-texts {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        flex-wrap: nowrap;
        max-width: 553px;
        width: 100%;
        padding: 50px 50px;

        .extended-banner-title {
            text-align: center;
            font-size: remMM(34);
            margin-top: 0;
            margin-bottom: 11px;
            font-weight: 800;
        }

        .extended-banner-text {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            flex-wrap: nowrap;
            text-align: center;
            font-size: remMM(16);
            margin-bottom: 25px;

            strong {
                font-size: remMM(142);
                margin-bottom: 50px;
                line-height: 1em;
            }
        }

        .extended-banner-link {
            @include trans(0.3s);
            padding: emMM(7) emMM(30) emMM(11) emMM(30);
            font-size: remMM(18);
            text-transform: uppercase;
            font-weight: 800;
            font-family: $default-font-family;
            background-color: $color-font;
            color: $color-font-second;

            &:hover,
            &:focus {

                background-color: $color-accent;
            }
        }


    }


    @media (max-width: 767px) {
        img {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
            width: 100%;
            height: 100%;
        }

        .extended-banner-texts {
            width: 100%;
            height: 100%;
            padding: 10px 10px;
            left: unset;
            top: unset;
            transform: unset;
            position: relative;

            .extended-banner-title {
                font-size: remMM(30);
            }

            .extended-banner-text {
                font-size: remMM(16);

                strong {
                    margin-bottom: 20px;
                    font-size: remMM(90);
                }
            }

            .extended-banner-link {
                &::before {}

                &:hover,
                &:focus {}
            }
        }
    }

    @media (max-width: 600px) {
        .extended-banner-texts {
            width: 100%;
            height: 100%;

            .extended-banner-title {}

            .extended-banner-text {
                font-size: remMM(16);

                strong {
                    font-size: remMM(70);
                }
            }

            .extended-banner-link {
                &::before {}

                &:hover,
                &:focus {}
            }
        }
    }

    a,
    img {
        display: block;
        width: 100%;
    }
}


.active-filters {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: nowrap;
    margin-bottom: 35px;
    order: -1;
    padding-top: 15px;
    border-top: 1px solid $black;

    &>div {
        flex: 0 1 auto;
    }

    .active-title {
        font-weight: 700;
        margin-right: 1em;

        padding: 0.5em 0;
    }

    #clear-filters {
        margin: 0;
        text-decoration: unset;
        display: flex;
        background-color: $color-danger;
        font-size: remMM(12);
        cursor: pointer;

        a {
            text-decoration: unset;
            padding: remMM(9) remMM(10) remMM(12) remMM(10);
            font-size: remMM(12);
        }

        &:hover {
            background-color: #ff3838;
        }
    }

    .filter-count {
        display: none;
    }

    .active-list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        margin-right: 10px;
        position: relative;
    }

    .filter-label {
        flex: 0 1 auto;
        // padding: 0.5em 1em;
        // border: 1px solid $color-border-dark;
        // border-radius: 4px;
        display: flex;
        position: relative;
        align-items: center;
        font-size: remMM(12);
        color: $color-font;
        padding: remMM(9) remMM(30) remMM(12) remMM(10);
        background-color: $black-10;

        &:before,
        &:after {
            content: '';
            position: absolute;
            width: 14px;
            height: 2px;
            background-color: black;
            top: 50%;
            right: 10px;
            border-radius: 5px;
            transform: translateY(-50%);
        }

        &:before {
            transform: rotate(45deg);
        }

        &:after {
            transform: rotate(-45deg);
        }

        &:hover,
        &:focus {

            &::after,
            &::before {
                background-color: #ff3838;
            }
        }
    }
}

.category__secondDescription {
    .columns {
        gap: 0;

        .column {
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 50px;
            max-width: 360px;

            @media (max-width: 1100px) {
                padding: 15px;
            }
        }

        @media (max-width: 767px) {
            flex-direction: column;
            gap: 30px;

            .column {
                max-width: unset;
                padding: 0;
            }
        }
    }
}

.pagination-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px;
    flex-direction: row-reverse;

    .pagination-top {
        display: none;
    }

    // .pagination-loader {
    //     margin-left: auto;
    // }

    .pagination {
        margin-right: auto;
        display: flex;
        align-items: center;
        gap: 8px;

        .current {
            order: 2;
            min-width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: $color-font-second;
            background-color: $color-font;
        }

        a {
            &[data-testid="linkFirstPage"] {
                min-width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                color: $color-font;
                background-color: $color-font-second;
                order: 1;
                margin-left: 32px;

                &:hover {
                    color: $color-font-second;
                    background-color: $color-font;
                }
            }

            &[data-testid="linkLastPage"] {
                min-width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                color: $color-font;
                background-color: $color-font-second;
                order: 3;
                margin-right: 32px;

                &:hover {
                    color: $color-font-second;
                    background-color: $color-font;
                }
            }
        }

        .pagination-link {
            @include trans(0.3s);
            display: flex;
            align-items: center;
            justify-content: center;
            // width: 34px;
            // height: 34px;
            // border-radius: 5px;
            background-color: #F7ECE4;
            // border: 1px solid $color-font;
            padding: emMM(6) emMM(20) emMM(6)emMM(20);
            border: 2px solid $color-font;
            font-size: remMM(16);
            color: $color-font-second;
            background-color: $color-font;
            text-decoration: none;
            font-weight: 700;

            &::before {
                // content: '';
                // display: block;
                // width: 10px;
                // height: 10px;
                // border: 2px solid $color-font;
                // border-style: none solid solid none;
                // transform: rotate(-45deg);
                // position: relative;
                // transition: all 0.15s ease-in-out;
                display: none;
            }

            // .sr-only {
            //     text-decoration: none;
            //     display: block;
            //     position: relative;
            //     overflow: unset;
            //     height: unset;
            //     width: unset;
            //     margin: 0;
            //     padding: 0;
            //     clip: unset;
            //     border: unset;
            // }

            &:hover {
                background-color: $color-font-second;
                color: $color-font;

                // &::before {
                //     border-color: $color-page-bg;
                // }
            }

            &.next {
                order: 4;
            }

            &.next::before {
                right: 2px;

            }

            &.prev {
                order: 0;
            }

            &.prev::before {
                transform: rotate(135deg);
                left: 2px;

            }
        }
    }

    .load-products {
        gap: 0.6em;
        color: $color-font;
        // border: 3px solid $color-font;
        // padding: emMM(8) emMM(18) emMM(10) emMM(18);
        // background-color: $color-page-bg;
        font-weight: 600;
        font-size: remMM(16);
        text-decoration: underline;
        background-color: unset !important;
        // &::before {
        //     content: '';
        //     display: block;
        //     width: 8px;
        //     height: 8px;
        //     border: 2px solid $color-font;
        //     border-style: none solid solid none;
        //     transform: rotate(45deg);
        //     position: relative;
        //     top: -2px;
        //     transition: all 0.15s ease-in-out;
        // }

        &:hover {
            color: $color-font;
            background-color: unset !important;
            text-decoration: none;
            // &::before {
            //     border-color: $color-page-bg;
            // }
        }
    }

    @media (max-width: 767px) {
        flex-direction: column;
        align-items: center;

        .pagination-loader {
            margin-left: 0;
            align-self: stretch;

            .load-products {
                width: 100%;
            }
        }



        .pagination {
            margin-right: auto;
            margin-left: auto;
            margin-top: 0;

            // [data-testid="linkFirstPage"] {
            //     margin-right: 0 !important;
            // }

            .pagination-link {

                background-color: unset;
                // border: 1px solid $color-font;
                padding: unset;
                width: 40px;
                height: 40px;
                border: unset;
                font-size: remMM(16);
                color: unset;
                background-color: unset;
                text-decoration: none;
                font-weight: 700;

                &::before {
                   // content: url(../img/icon/arrow-right.svg);
                    display: block;
                    width: 24px;
                    height: 24px;


                    // transform: rotate(-45deg);
                    position: relative;
                    transition: all 0.15s ease-in-out;
                    // display: none;
                }

                .sr-only {
                    display: none;
                }

                &:hover {
                    background-color: unset;
                    color: unset;
                }

                &.next {
                    order: 4;
                }

                &.next::before {
                    right: 2px;

                }

                &.prev {
                    order: 0;
                }

                &.prev::before {
                    transform: rotate(-180deg);
                }
            }
        }
    }
}

