    }
}

// CONTENT
#content .text,
.basic-descripton {
    ul {
        padding: 0;
        list-style: none;

        li {
            display: flex;
            align-items: center;

            &::before {
                content: '';
                display: block;
                width: 6px;
                height: 6px;
                background-color: $color-accent;
                border-radius: 50%;
                margin: 11px;
            }
        }
    }

    ol {
        padding: 0;
        list-style: none;
        counter-reset: my-counter;

        li {
            display: flex;
            align-items: flex-start;
            counter-increment: my-counter;

            &::before {
                content: counter(my-counter) ')';
                display: block;
                text-align: right;
                color: $color-accent;
                margin-right: 10px;
                font-weight: 700;
                min-width: emMM(24);
            }
        }
    }
}

// PAGES
.type-page {
    .content {
        h1 {
            text-align: center;
        }
    }
}

.id--12,
.customer-page {
    .content-inner {
        @media screen and (max-width: 1440px) {
            overflow-x: auto;
        }

        table {

            thead,
            tbody {
                width: max-content;
                white-space: nowrap;
            }
        }
    }
}